<template>
    <div class="feedCon">
        <div class="feedContend">
            <span>姓名</span>
            <input type="text" v-model="userName">
        </div>
        <div class="feedContend">
            <span>联系电话</span>
            <input type="text" v-model="userTel">
        </div>
        <div class="feedContend">
            <span>电子邮箱</span>
            <input type="text" v-model="userEmail">
        </div>
        <div class="feedContend1">
            <span>描述</span>
            <textarea @focus="foc()" name="" id="" placeholder="请尽可能您的相关问题和建议,这将更好地帮助我们改进产品和服务" v-model="userContent">               
            </textarea>
        </div>
        <div class="feedHelp">
            <img src="../assets/icon_zixun.png" alt="暂无图片">
            <p>
                您有什么想法和意见可以直接给小查公众号留言,也可以通过<a href="..">QQ315072078</a>反馈我们
            </p>
        </div>
        <div class="feedSub">
            <button class="btn1">重置</button>
            <button class="btn2" @click='submit()'>提交</button>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import qs from 'qs'
export default {
    name:'Feedback',
    data(){
        return{
            userName:'',
            userTel:'',
            userEmail:'',
            userContent:''
        }
    },
    methods:{
        foc:function(){
            console.log(this.userContent)
        },
        submit:function(){
            this.axios.post('api/interact/message',qs.stringify({
                username:this.userName,
                usertel:this.userTel,
                useremail:this.userEmail,
                content:this.userContent
            })).then(function(res){
                console.log(res)
            }).catch(function(error){
                console.log(error)
            })
        }
    }
}
</script>
<style scoped>
    .feedCon{
        border: .1px solid #fff;
    }
    .feedContend{
        width:100%;
        height:.7rem;
        line-height: .7rem;
        background: #fff;
        display: -webkit-flex;
        display: flex;
        margin-top: .2rem;
    }
    .feedContend span{
        text-align: left;
        font-size: .2rem;
        color: #000;
        margin-left: .2rem;
        width: 1.6rem;
    }
    .feedContend input{
        flex: 1;
        height:.6rem;
        line-height: .6rem;
        border: none;
        outline: none;
    }
    .feedContend1{
        width:100%;
        height:.7rem;
        line-height: .7rem;
        background: #fff;
        display: -webkit-flex;
        display: flex;
        margin-top: .2rem;
        /* flex-direction: row; */
    }
    .feedContend1 span{
        text-align: left;
        font-size: .2rem;
        color: #000;
        margin-left: .2rem;
        width: 1.6rem;
    }
    .feedContend1 textarea{
        width: 100%;
        height: 3rem;
        margin-top: .7rem;
        margin-left: -1.8rem;
        padding-left: .3rem;
        resize: none;
        border: none;
        outline: none;
        color: #333;
    }
    .feedHelp{
        width: 100%;
        height: 1.5rem;
        margin-top:3.1rem; 
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
        font-size: .1rem;
        text-align: left;
    }
    .feedHelp img{
        transform: scale(0.4)
    }
    .feedSub{
        position: fixed;
        bottom: 20px;
        z-index: 200;
        width: 96%;
        background: #fff;
        padding-left: .2rem;
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
    }
    .feedSub button{
        width: 47%;
        height: .8rem;
        line-height: .8rem;
        border: none;
        outline: none;
    }
    .btn1{
        background: #eee;
    }
    .btn2{
        background: linear-gradient(90deg,rgba(0,218,254,1),rgba(0,75,226,1));
    }
</style>




