import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home' //首页
import Conent from '@/components/Conent' //二级路由
import Feedback from '@/components/Feedback'  //服务号反馈
import Validity from '@/components/Validity'  //三级路由--标准
import Invalid from '@/components/Invalid'  //标准作废
import Instead from '@/components/Instead'  //标准代替
import Lack from '@/components/Lack'  //标准缺失
import Collection from '@/components/Collection'  //收藏
import Classify from '@/components/Classify'  //分类查询1
import ClassifyTwo from '@/components/ClassifyTwo'  //分类查询2
import High from '@/components/High'  //高级查询1
import HighTwo from '@/components/HighTwo'  //高级查询2
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/Home',
      name: 'Home',
      component: Home
    },{
      path:'/Conent',
      component: Conent,
      children:[
        {
          path:'/Validity',
          name:'Validity',
          component:Validity,
          children:[
            {
              path:'/Invalid',
              name:'Invalid',
              component:Invalid
            },{
              path:'/Instead',
              name:'Instead',
              component:Instead
            },{
              path:'/Lack',
              name:'Lack',
              component:Lack
            }
          ]
        },{
          path:'/Feedback',
          name:'Feedback',
          component:Feedback
        }
      ]
    },{
      path:'/Collection',
      name:'Collection',
      component:Collection,
      meta:{
        'title':'收藏',
      }
    },{
      path:'/Classify',
      name:"Classify",
      component:Classify
    },{
      path:'/ClassifyTwo',
      name:'ClassifyTwo',
      component:ClassifyTwo
    },{
      path:'/High',
      name:'High',
      component:High
    },{
      path:'/HighTwo',
      name:'HighTwo',
      component:HighTwo
    }
  ]
})



