<template>
    <div class="high">
        <div class="highDiv">
            <div class="standardTitle">标准号</div>
            <div class="standardCon">GB 5744684</div>
        </div>
        <div class="highDiv">
            <div class="standardTitle">标准名称</div>
            <div class="standardCon">GB 5744684</div>
        </div>
        <div class="highDiv mTop">
            <div class="standardTitle">主管部门</div>
            <div class="standardCon"></div>
        </div>
        <div class="highDiv">
            <div class="standardTitle">发布单位</div>
            <div class="standardCon"></div>
        </div>
        <div class="highDiv time">
            <div class="standardTitle">发布日期</div>
            <div class="standardCon standardConTime">
                <input type="month" name="" id="">
                <span>到</span>    
                <input type="month" name="" id="">
            </div>
        </div>
        <div class="highDiv time">
            <div class="standardTitle">实施日期</div>
            <div class="standardCon standardConTime">
                <input type="month" name="" id="">
                <span>到</span>    
                <input type="month" name="" id="">
            </div>
        </div>
        <div class="highDiv mTop">
            <div class="standardTitle">行业</div>
            <div class="trade">XXX行业</div>
            <div class="standardCon conRight">
                <img :src="packUp" alt="正在加载" @click='tradeShow()'>
            </div>
        </div>
        <div class="highDiv">
            <div class="standardTitle">标准状态</div>
            <div class="standardCon flexTwo">全部</div>
            <div class="standardCon conRight">
                <img :src="unflod" alt="">
            </div>
        </div>
        <div class="highDiv">
            <div class="standardTitle">效果排序</div>        
            <div class="standardCon standardConImg"><img :src="inputChecked ? checked : unchecked" alt="暂无图片">标准名称</div>
            <div class="standardCon standardConImg"><img :src="inputChecked ? checked : unchecked" alt="暂无图片">发布时间</div>
            <div class="standardCon standardConImg"><img :src="inputChecked ? checked : unchecked" alt="暂无图片">标准状态</div>
        </div>
        <div class="highDiv">
            <div class="standardTitle">字段间关系</div>            
            <div class="standardCon standardConImg"><img :src="inputChecked ? checked : unchecked" alt="暂无图片">并且</div>
            <div class="standardCon standardConTwo"><img :src="inputChecked ? checked : unchecked" alt="暂无图片">或者</div>           
        </div>
        <div class="btnDiv">
            <button class="btn1">重置</button>
            <button class="btn2">查询</button>
        </div>
        <transition name="slide">
            <div class="tradeDiv" v-show="tradeDiv">
                <div class="shadow" @click.stop='tradeDiv=false'></div>
                <div class="tradeList">
                    <div>1311423213</div>
                </div>
            </div>
        </transition>         
    </div>
</template>
<script>
import packUp from '../assets/icon_in.png'
import unflod from '../assets/icon_sanjiao.png'
import unchecked from "../assets/button_nor_xuankuang.png"
import checked from "../assets/button_sel_xuankuang.png"
export default {
    name:'High',
    data(){
        return{
            packUp,
            unflod,
            checked,
            unchecked,
            inputChecked:false,
            tradeDiv:false
        }
    },
    methods:{
        tradeShow:function(){
            this.tradeDiv=true;
        }
    }
}
</script>
<style scoped>
    .high{
        width: 100%;
        background: #ececec;
    }
    .high img{
        transform: scale(0.6)
    }
    .highDiv{
        width: 99%;
        height: .8rem;
        background: #fff;
        color: #333;
        font-size: .2rem;
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        text-align: left;
        padding-left: .2rem;
        border-bottom: 1px solid #ececec;
    }
    .mTop{
        margin-top: .2rem;
    }
    .standardTitle{
        flex: 1;
    }
    .standardCon{
        flex: 3;
    }
    .trade{
        flex: 2;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
    .standardConTime{
        display: -webkit-flex;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .standardConImg{
        flex:1;
        display: -webkit-flex;
        display: flex;
        align-items: center;
    }
    .time input{
        width: 2.2rem;
        border: none;
        outline: none;
    }
    .conRight{
        flex:1;
        display: -webkit-flex;
        display: flex;
        justify-content: flex-end;
        padding-right: .2rem;
    }
    .flexTwo{
        flex:2;
    }
    .standardConTwo{
        flex: 2;
        display: -webkit-flex;
        display: flex;
        align-items: center;
    }
    .btnDiv{
        width: 100%;
        height: 1rem;
        background: #fff;
        position: fixed;
        left: 0;
        bottom:2px;
        display: -webkit-flex;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .btnDiv button{
        height: 80%;
        width: 45%;
        border: none;
        outline: none;
    }
    .btn1{
        color: #63CDDF;
        background: #eee;
        border-radius: 6px 0px 0px 6px;
    }
    .btn2{
        color: #fff;
        background:linear-gradient(90deg,rgba(0,218,254,1),rgba(0,75,226,1));
        border-radius:0px 6px 6px 0px;
    }
    .tradeDiv{
        width: 100%;
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
        position: fixed;
        top:0;
        left:0;
        z-index: 222;       
    }
    .tradeDiv-active{
        animation: slide 12s;
    }
    .shadow{
        flex: 1;
        background: #000;
        opacity: 0.5;
    }
    .tradeList{
        flex: 3;
        background: #fff;
        padding-left: .2rem;
    }
    .tradeList div{
        display: -webkit-flex;
        display: flex;
        align-items: center;
        height: .8rem;
        font-size: .3rem;
        border-bottom:1px solid #ececec; 
    }
    @keyframes slide{
        0%{
            left: 100%;
            color: red;
        }
        50%{
            left:50%;
            color: black;
        }
        100%{
            left:0%;
            color: blue;
        }
    }
</style>



