<template>
    <div class="invalid">
        <div class="invalidCon">
            <span>标准号</span>
            <input type="text" name=''>
        </div>
        <div class="invalidCon">
            <span>标准名称</span>
            <input type="text" name=''>
        </div>
        <div class="invalidCon1">
            <span>描述</span>
            <textarea name="" id="" cols="12"></textarea>
        </div>
        <div class="feedSub">
            <button class="btn1">重置</button>
            <button class="btn2">提交</button>
        </div>
    </div>
</template>
<script>
export default {
    name:'Invalid',
    data(){
        return{

        }
    }
}
</script>
<style scoped>
    .invalid{
        background: #ccc;
    }
    .invalidCon{
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
        width: 100%;
        height:.7rem;
        line-height: .7rem;
        background: #fff;
        padding-left:.2rem; 
        margin-top: .2rem;
        font-size: .2rem;
        color: #000;
    }
    .invalidCon span{
        text-align: left;
        width: 1.8rem;
    }
    .invalidCon input{
        flex: 1;
        border: none;
        outline: none;
    }
    .invalidCon1{
        display: -webkit-flex;
        display: flex;
        width: 98%;
        height:2.4rem;
        background: #fff;
        padding-left: .2rem;
        padding-top: .2rem;
        margin-top: .2rem;
        font-size: .2rem;
        color: #000;
    }
    .invalidCon1 span{
        display: block;
        text-align: left;
        width: 100%;
        height: .3rem;
    }
    .invalidCon1 textarea{
        width: 1000%;
        height:1.2rem;
        text-align: left;
        margin-top: .5rem;
        margin-left: -.7rem;
        border: none;
        resize: none;
        outline: none;
        color: #333;
    }
    .feedSub{
        position: fixed;
        bottom: 1px;
        z-index: 200;
        width: 96%;
        background: #fff;
        padding-left: .2rem;
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
    }
    .feedSub button{
        width: 47%;
        height: .8rem;
        line-height: .8rem;
        margin-top: .2rem;
        border: none;
        outline: none;
    }
    .btn1{
        background: #eee;
    }
    .btn2{
        background: linear-gradient(90deg,rgba(0,218,254,1),rgba(0,75,226,1));
    }
</style>





