<template>
    <div class="collection">
        <div v-if="resource">
            <!-- 头部搜索框 -->
            <div class="searchDiv">
                <input type="text" placeholder="页内搜索">
            </div>
            <!-- 内容展示区 -->
            <div class="collCon">
                <div class='imgDiv' @click='checkCon()'>
                    <img :src='imgShow ? unchecked : checked' alt="">
                </div>
                <div class='contentDiv'>
                    <b><p class='conTitle'>我的打打打ewewe分是大发发1321111111顺丰三</p></b>
                    <p class='conCent'>12345</p>
                </div>
            </div>
            <!-- 底部按钮 -->
            <transition>
                <div class="btnDiv" v-show="btnshow">
                    <button>取消</button>
                    <button>全选</button>
                    <button>删除</button>
                </div>
            </transition>        
        </div>
        <div v-if="!resource" class="unCon">
            <img class="unConimg" src="../assets/pic_weikong.png" alt="">
        </div>
    </div>
</template>
<script>
import unchecked from '../assets/button_nor_xuankuang.png'
import checked from '../assets/button_sel_xuankuang.png'
import qs from 'qs'
export default {
    name:'Collection',
    data:function(){
        return{
            resource:true,
            imgShow:false,
            btnshow:false,
            unchecked:unchecked,
            checked:checked
        }
    },
    mounted:function(){
        this.axios.post('api/collection/search',qs.stringify({})).then(function(res){
            
        }).catch(function(error){
           
        })
    },
    methods:{
        //左边按钮单击选中
        checkCon:function(){           
            // if(this.imgurl==unchecked){
            //     this.imgurl=checked;
            // }else{
            //     this.imgurl=unchecked;
            // }
            this.imgShow=!this.imgShow;
            this.btnshow=true;
        }
        
    }
}
</script>
<style scoped>
    .unCon{
        display: -webkit-flex;
        display: flex;
        margin: auto;
        align-content: center;
        background: #ececec;
        width: 100%;
        height: 100%;
    }
    .unCon img{
        transform: scale(0.5)
    }
    .collection{
        background: #ccc;
        border: 1px solid #ccc;
    }
    .searchDiv{
        display: -webkit-flex;
        display: flex;
        margin-left: .2rem;
        margin-right: .2rem;
        margin-top: .2rem;
        width: 94%;
        height:.4rem;
        background: #fff;
        border: 1px solid #fff;
        color: #000;
        border-radius: 5px;
    }
    .searchDiv input{
        width: 100%;
        height:.4rem;
        line-height: .4rem;
        border: none;
        outline: none;
    }
    .searchDiv input::placeholder{
        text-align: center;
    }
    .collCon{
        display: -webkit-flex;
        display: flex;
        background: #fff;
        margin-top: .2rem;
    }
    .imgDiv{
        display: -webkit-flex;
        display: flex;
        width: 20%;
    }
    .imgDiv img{
        margin: auto;
        align-content: center;
    }
    .contentDiv{
        width: 78%;
    }
    .conTitle{
        font-size: .3rem;
        text-align: left;
        word-break: break-all;
        word-wrap:break-word;
        margin-top: -.05rem;
        padding-top: .15rem;
    }
    .conCent{
        text-align: left;
        font-size: .2rem;       
        margin-top: -.1rem;
    }
    .btnDiv{
        position: fixed;
        bottom: 2px;
        left:0;
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
        width: 100%;
        height: 1rem;
        line-height: 1rem;
        animation: slip-into 4s;
    }
    .btnDiv button{
        background: blue;
        color: #fff;
        flex: 1;
        border: none;
        outline: none;
    }
    @keyframes slip-into{
        0%{
            left:100%;
        };
        50%{
            left: 50%;
        };
        100%{
            left: 0;
        }
    }
</style>




