<template>
  <div class="Home">
    <div class='homeTop'>
        <div class="homeTopLeft">
          <img src="../assets/logo.png" alt="">
        </div>
        <div class='homeTopContent'>
          <h3>标准查</h3>
          <p>查标准就上标准查</p>
        </div>
    </div>
    <div class="introduce">
      <p :class='{pShow:pShow}'>
        我们致力于为企业事业单位搭建最专业的标准查询体系建设,
        我们致力于为企业事业单位搭建最专业的标准查询体系建设,              
        我们致力于为企业事业单位搭建最专业的标准查询体系建设,
        我们致力于为企业事业单位搭建最专业的标准查询体系建设。
        欢迎一起学习!
      </p> 
      <button v-show='btnShow' @click='btnClick()'>
        品牌介绍
        <img src="../assets/icon_xiala.png" alt="">
    </button>       
    </div>
    <div class="consult">
      <h2>服务咨询</h2>
      <p>为了更好的给您提供专业的服务,请详细的填写以下信息</p>
      <div>
        <div class='froms'>
          <span>公司名称</span>
          <input type="text">
        </div>
        <div class='froms'>
          <span>部门</span>
          <input type="text">
        </div>
        <div class='froms'>
          <span>联系人姓名</span>
          <input type="text">
        </div>
        <div class='froms'>
          <span>电话</span>
          <input type="text">
        </div>
      </div>
      <div v-if='unforms'>
        <div class='froms'>
          <span>职位</span>
          <input type="text">
        </div>
        <div class='froms'>
          <span>微信</span>
          <input type="text">
        </div>
        <div class='froms'>
          <span>QQ</span>
          <input type="text">
        </div>
        <div class='froms'>
          <span>邮箱</span>
          <input type="text">
        </div>
        <div class='froms'>
          <span>留言</span>
          <input type="text">
        </div>
      </div>
      <div class="submitDiv">
          <button class="sbtn1" @click="showfrom()">{{btnText}}<img :src='imgurl' alt=""></button>
          <button class='sbtn2' @click='submitfrom()'>提交信息</button>
      </div>
    </div>
  </div>
</template>
<script>
import imgtop from '../assets/icon_shousuo1.png'
import imgbot from '../assets/icon_xiala.png'
export default {
  name: 'Home',
  data () {
    return {
      btnShow:true,
      pShow:true,
      unforms:false, //多余的表单先隐藏
      btnText:'填写更多',
      imgurl:imgbot,
    }
  },
  methods:{
    btnClick:function(){
      this.btnShow=false;
      this.pShow=false;
    },
    showfrom:function(){
      this.unforms=!this.unforms;
      if(this.unforms==true){       
        this.btnText='收起';
        this.imgurl=imgtop;
      }else{
        this.btnText='填写更多';
        this.imgurl=imgbot;
      }      
    }
  }
}
</script>
<style scoped>
  .Home{
    width: 100%;
    background-color: rgb(0,75,226);
  }
  .homeTop{
    display: -webkit-flex;
    display: flex; 
  }
  .homeTopLeft{
    width: 2.5rem;
    margin-left: -.4rem;
  }
  .homeTop img{
    transform: scale(0.6)
  }
  .homeTopContent{
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    flex: 1;
  }
  .homeTopContent h3{
    margin-top: .8rem;
    font-size: .5rem;
    color: #fff;
  }
  .homeTopContent p{
    font-size: .2rem;
    color: #fdf;
    margin-top: -.3rem;
  }
  .introduce{
    margin-top: -.7rem;
    font-size: .1rem;
    padding-left: .1rem;
    padding-right: .1rem;
    color: #fef;
    height:2rem;
  }
  .pShow{
    text-overflow: -o-ellipsis-lastline;  
    overflow: hidden;  
    text-overflow: ellipsis;  
    display: -webkit-box;  
    -webkit-line-clamp: 1;  
    -webkit-box-orient: vertical;
  }
  .introduce button{
    margin:auto;
    opacity: .9;
    border: 1px solid #ccc;
    border-radius: 31px;
    background: none;
    color: #fff;
    position: relative;
    top: .7rem;
  }
  button img{
    transform: scale(0.6)
  }
  .consult{
    background: #fff;
    margin: .2rem;
  }
  .consult h2{
    font-size: .5rem;
  }
  .consult p{
    font-size: .1rem;
    margin-top: -.3rem;
    color: #ccc;
  }
  .froms{
    border-bottom: 1px solid #ccc;
    font-size: .15rem;
    display: -webkit-flex;
    display: flex;
    justify-content: flex-start; 
    margin-left: .2rem;
    margin-right: .1rem;
    height: .7rem;
    line-height: .7rem;
  }
  .froms span{
    padding-left: .3rem;
    display: block;
    width: 1.6rem;
    text-align: left;
  }
  .froms input{
    flex: 1;
    border-width:0;
    outline: none; 
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
  }
  .submitDiv{
    display: -webkit-flex;
    display: flex;
    justify-content: space-around;
    height:2rem;
    align-items: center;
    margin-bottom: .2rem;

  }
  .submitDiv button{
    width: 2rem;
    height: .6rem;
  }
  .sbtn1{
    background: rgb(246,249,255);
    color: rgb(103,103,103);
    border: none;
    border: 1px solid rgb(101,103,103);
  }
  .sbtn1 img{
    transform: scale(0.5);
    filter: drop-shadow(#000 2px 0);
  }
  .sbtn2{
    background: linear-gradient(rgb(0,216,255), rgb(0,78,288));
    color: #fefefe;
    border: none;
  }
</style>



