<template>
    <div class="classify2">
        <div class="company"> 
            <div class="companyName">福克斯通科技有限公司标准体系</div>
            <div class="unsenen"><img src="../assets/icon_in.png" alt="暂无图片"></div>
        </div>              
        <div class="industry">  <!-- 行业标准内容外部div   -->
            <div class="stair">
                <div class="standardTitle"> <!--一级标题-->
                    <img :src="packUp" alt="暂无图标">
                    燃气技术研究院标准体系
                </div>
            </div>
            <div class="stair">  <!--一级分类-->
                <div class="standardTitle"> <!--标题-->
                    <img :src="unflod" alt="暂无图标">
                    石油管道标准体系
                </div>
                <div class="access" v-if='accessShow'> <!--二级分类-->
                    <div class="standardTitle"> <!--标题-->
                        <img :src="unflod" alt="暂无图标">
                        二级菜单标题
                    </div>
                    <div class="access">
                        <div class="standardTitle"> <!--标题-->
                            <img :src="unflod" alt="暂无图标">
                            三级菜单标题
                        </div>
                    </div>
                </div>
                <div class="access" v-if='accessShow'> <!--二级分类-->
                    <div class="standardTitle"> <!--标题-->
                        <img :src="packUp" alt="暂无图标">
                        二级菜单标题
                    </div>
                    <div class="standardTitle"> <!--标题-->
                        <img :src="packUp" alt="暂无图标">
                        二级菜单标题
                    </div>
                </div>
                <div class="access" v-if='accessShow'> <!--二级分类-->
                    <div class="standardTitle"> <!--标题-->
                        <img :src="packUp" alt="暂无图标">
                        二级菜单标题
                    </div>
                </div>
            </div>
            <div class="stair">
                <div class="standardTitle"> <!--一级标题-->
                    <img :src="packUp" alt="暂无图标">
                    燃气技术研究院标准体系
                </div>
            </div>
        </div>
        <div class="btnDiv">
            <button class="btn1">取消</button>
            <button class="btn2">确定</button>
        </div>
    </div>
</template>
<script>
import packUp from '../assets/icon_shousuo.png'
import unflod from '../assets/icon_zhankai.png'
export default {
    name:'ClassifyTwo',
    data(){
        return{
            accessShow:true,
            packUp,
            unflod
        }
    }
}
</script>
<style scoped>
    .classify2{
        width: 100%;
        height: 100%;
        background: #ececec;
    }
    .classify2 img{
        transform: scale(0.6);
    }
    .company{
        padding-left:.2rem; 
    }
    .company{
        display: -webkit-flex;
        display: flex;
        align-items: center;
        font-size: .3rem;
        width: 98%;
        height: .8rem;
        background: #fff;
        color: #333;
        margin-bottom: .2rem;
        text-align: left
    }
    .companyName{
        flex: 6;
    }
    .unsenen{
        flex:.5;
    }
    .industry{
        margin-top: .2rem;
        font-size: .3rem;
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
    }
    .stair,.access{
        width: 100%;
        min-height: .8rem;
        background: #fff;
        display: -webkit-flex;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
        padding-left: .2rem;
    }
    .stair{
        border-bottom: 1px solid #ececec;
    }
    .standardTitle{
        width: 100%;
        min-height: .8rem;
        display: -webkit-flex;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
    }
    .btnDiv{
        width: 100%;
        height: 1rem;
        background: #fff;
        position: fixed;
        left: 0;
        bottom:2px;
        display: -webkit-flex;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .btnDiv button{
        height: 80%;
        width: 45%;
        border: none;
        outline: none;
    }
    .btn1{
        color: #63CDDF;
        background: #eee;
        border-radius: 6px 0px 0px 6px;
    }
    .btn2{
        color: #fff;
        background:linear-gradient(90deg,rgba(0,218,254,1),rgba(0,75,226,1));
        border-radius:0px 6px 6px 0px;
    }
</style>



