<template>
    <div class="classify">
        <div class="subtitle">标准分类游览</div>
        <div class="nameDiv">
            <span class="company">福克斯通科技有限公司</span>
            <span class="unsenen">></span>
            <span class="trade">福克斯通科技有限公司</span>
            <span class="unsenen">></span>
            <span class="stand">福克斯通科技技术有限公司</span>
            <span class="unsenen1"><img src="../assets/icon_in.png" alt="暂无图片"></span>
        </div>
        <div class="inquire">
            <div>查询项</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">标准号</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">标准名称</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">发布单位</div>
        </div>
        <div class="crux">
            <div>关键词</div>
            <div>中国石油天然气管道XXXX</div>
        </div>
        <div class="category">
            <div>类别</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">国家标准</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">行业标准</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">企业标准</div>
        </div>
        <div class="status">
            <div>关键词</div>
            <div>全部</div>
            <div><img src="../assets/icon_sanjiao.png" alt="暂无图片"></div>
        </div>
        <div class="rank">
            <div>结果排序</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">国家标准</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">行业标准</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">企业标准</div>
        </div>
        <div class="asc">
            <div>结果排序</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">并且</div>
            <div><img :src="inputChecked ? checked : unchecked" alt="暂无图片">或者</div>
        </div>
        <div class="btnDiv">
            <button class="btn1">重置</button>
            <button class="btn2">查询</button>
        </div>
    </div>
</template>
<script>
import unchecked from "../assets/button_nor_xuankuang.png"
import checked from "../assets/button_sel_xuankuang.png"
export default {
    name:"Classify",
    data(){
        return{
            inputChecked:false,
            checked,
            unchecked
        }
    }
}
</script>
<style scoped>
    .classify{
        width: 100%;
        background: #ececec;
    }
    .subtitle{
        width: 100%;
        height:.6rem;
        font-size: .2rem;
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
        padding-left: .2rem;
        padding-bottom: .1rem;
        color: #a2a2a2;
    }
    .nameDiv{
        width: 98%;
        height:.8rem;
        background: #fff;
        font-size: .3rem;
        display:-webkit-flex;
        display: flex;    
        justify-content: start;
        align-items: center;
        color: #333;   
        padding-left: .2rem;
    }
    .company,.trade{
        flex:2.8;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
        margin-left: -.1rem;
    }
    .unsenen{
        flex:1;
    }
    .unsenen1{
       position: relative;
       right: .2rem;
    }
    .classify img{
        transform: scale(0.6);
    }
    .stand{
        flex:13;
        margin-left: -.7rem;
    }
    .inquire,.crux,.category,.status,.rank,.asc{
        height: .8rem;
        display: -webkit-flex;
        display: flex;
        justify-content: flex-start;
        font-size: .3rem;
        background: #fff;        
        padding-left:.2rem; 
    }
    .inquire,.category{
        margin-top: .2rem;
    }
    .crux,.status,.rank,.asc{
        border-top:1px solid #ececec;
    }
    .inquire div,.crux div,.category div,.status div,.rank div,.asc div{
        flex: 1;
        height: 100%;
        display: -webkit-flex;
        display: flex;
        align-items: center;
    }
    .crux div:nth-child(2){
        flex: 3;
    }
    .status div:nth-child(2){
        flex: 2;
    }
    .status div:nth-child(3){
        justify-content: flex-end;   
        position: relative;
        right: .2rem;     
    }
    .asc div:nth-child(3){
        flex:2;
    }
    .btnDiv{
        width: 100%;
        height: 1rem;
        background: #fff;
        position: fixed;
        left: 0;
        bottom:2px;
        display: -webkit-flex;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .btnDiv button{
        height: 80%;
        width: 45%;
        border: none;
        outline: none;
    }
    .btn1{
        color: #63CDDF;
        background: #eee;
        border-radius: 6px 0px 0px 6px;
    }
    .btn2{
        color: #fff;
        background:linear-gradient(90deg,rgba(0,218,254,1),rgba(0,75,226,1));
        border-radius:0px 6px 6px 0px;
    }
</style>




